<template>
  <div>
    <iframe scrolling="no" width="300" height="400" frameBorder="0" allowTransparency="true" :src="setSrc"></iframe>
  </div>
</template>

<script>
export default {
  data () {
    return {
      src: 'https://open.weixin.qq.com/connect/qrconnect?appid=wxe1f5def243e0390b&scope=snsapi_login&redirect_uri=https://abstest.tenpay.com/abs/author/callBack.do&state=0001&login_type=jssdk&self_redirect=default&style=black&href=./wx.css'
    }
  },
  computed: {
    setSrc () {
      var _url = 'https://open.weixin.qq.com/connect/qrconnect?appid=' + this.appid + '&scope=' + this.scope + '&redirect_uri=' + this.redirect_uri + '&state=' + this.state + '&login_type=jssdk&self_redirect=default&style=' + this.theme + '&href=' + this.href
      return _url
    }
  },
  props: {
    // 
    appid: String,
    // ,snsapi_login
    scope: {
      type: String,
      default: 'snsapi_login'
    },
    // UrlEncode
    redirect_uri: String,
    // csrfsession
    state: {
      type: String,
      default: ''
    },
    // "black""white"FAQ
    theme: {
      type: String,
      default: 'black'
    },
    // FAQ
    href: {
      type: String,
      default: ''
    }
  }
}
</script>
